<template>
  <div class="col-sm-12 auto select-overspread">
    <div class="auto select-overspread">
      <div class="panel panel-info">
      	<div class="panel-heading auto">
      		<h3 class="panel-title">终止报建</h3>
      	</div>
      </div>
    </div>
    <validator name="v">
    <form class="horizontal" style="padding-top: 50px;">

      <div class="form-group col-sm-12">
        <div class="col-sm-2">
          <label class="control-label col-sm-12">终止原因：</label>
        </div>
        <div class="col-sm-10">
          <textarea class="form-control" rows="3" v-model="model.f_stop_remarks"></textarea>
        </div>
      </div>
      <div style="height:200px;"></div>
      <div class="form-group col-sm-12">
        <div class="col-sm-6">
          <label class="control-label col-sm-4">分公司：</label>
          <div class="col-sm-8">
            <input class="form-control" type="text" v-model="model.f_filiale"  disabled>
          </div>
        </div>
        <div class="col-sm-6">
          <label class="control-label col-sm-4">操作日期：</label>
          <div class="col-sm-8">
            <input class="form-control" type="text" v-model="model.f_date"  disabled>
          </div>
        </div>
      </div>
      <div class="form-group col-sm-12">
        <div class="col-sm-6">
          <label class="control-label col-sm-4">部门：</label>
          <div class="col-sm-8">
            <input class="form-control" type="text" v-model="model.f_department"  disabled>
          </div>
        </div>
        <div class="col-sm-6">
          <label class="control-label col-sm-4">操作人：</label>
          <div class="col-sm-8">
            <input class="form-control" type="text" v-model="model.f_operator"  disabled>
          </div>
        </div>
      </div>
    </form>
    </validator>
    <div class="from-group col-sm-12">
      <center>
        <button type="button" @click="confirm()" class="btn btn-primary" style="width:80px; margin-left:50px;" :disabled="$v.invalid">
          保存
        </button>
        <button  type="button" @click="reset()" class="btn btn-warning" style="width:80px; margin-left:50px;">
          返回
        </button>
      </center>
    </div>
  </div>
</template>
<script>
  // import AppData from '../../../../stores/AppData'
  import * as Util from '../../../Util'
  import co from 'co'
  let gen = function * (self, val, val1) {
    let data ={
      tablename:'t_apply',
      parameters:val1
    }
    yield self.$resetpost('rs/logic/save', {data:data})
    delete val.id
    // val.f_cancel_reason = self.stopapply.f_process_id
    val.processid = self.stopapply.f_process_id
    val.f_operator = self.$login.f.name
     data ={
      tablename:'t_stopapply',
      parameters:val
    }
    self.$resetpost('rs/logic/save', {data:data})
    .then(() => {
      self.workflow()
    })
  }
  export default {
    title: '终止报建',
    data () {
      return {
        model: {
          f_date: Util.toStandardTimeString(),
          f_department: this.$login.f.f_parentname,
          f_operator: this.$login.f.name,
          f_filiale: this.$login.f.f_fengongsi
        }
      }
    },
    props: ['stopapply'],
    methods: {
      confirm () {
        this.model = Object.assign({}, this.stopapply, this.model)
        this.model.id = this.stopapply.id
      // this.model.f_jmhj_cost= this.model.f_fee
      //   this.stopapply.f_state = '终止报建'
        this.model.applyid = this.stopapply.id
        this.model.f_operat_type = '终止报建'
        this.model.f_state = '有效'
        this.model.f_describe = this.model.f_user_name + '已终止报建'
        let getGen = gen(this, this.model, this.stopapply)
        co(getGen)
        this.$dispatch('searchcard')
        this.$dispatch('hiddenbtn')
      },
      workflow () {
        // this.$post(`rs/workflow/stop/${this.stopapply.actid}/${this.$login.f.name}/${this.$login.f.id}`, [{data: `{id: ${this.model.actid}}`}])
        this.$resetpost('rs/logic/stopapply', {data: this.model})
        .then((res) => {
          this.reset()
        })
      },
      reset () {
        this.$dispatch('hiddenbtn')
        // this.$parent.$refs.installselect.$refs.cp.$refs.cri.search()
      }
    }
  }
</script>



